.App {
  text-align: center;
}

.CarTireRight {
  animation: App-logo-spin infinite 1s linear;
  height: 140px;
  pointer-events: none;
  position: absolute;
  left: 550px;
  top: 530px;
  z-index: 1;
}

.CarTireLeft {
  animation: App-logo-spin infinite 1s linear;
  height: 140px;
  pointer-events: none;
  position: absolute;
  left: 1066px;
  top:530px;
  z-index: 1;
}
.Car{
  animation: Jumpping infinite .1s linear;

}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes Jumpping{
  from{
    transform: rotateX(10deg)
  }
  to{
    transform: rotateY(2deg)
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
